import React, { PureComponent } from "react";
import Image from "../../Elements/Media/Image";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import { isTagEmbed } from "../../../../actions/api";
export default class Media extends PureComponent {

  render() {
    const { itemData, wallID, themeID, ownerId } = this.props;
    return (
      <div className="tb_gp_media_wrap">
        <MediaIcon itemData={itemData}  IconClass="tb_gp_media_icon" position={1} show={true} isCenter={isTagEmbed}/>
        <Image ImageClass="tb_gp_image" data={itemData} wallID={wallID} themeID={themeID} ownerId={ownerId} size={100} />
      </div>
    );
  }
}