import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import Network from "../../Elements/Network/Network";
import { themeImpressionTracking } from "../../../../utils";
import { isTagEmbed, isTagShop, isTagbox, suiteTrack } from "../../../../actions/api";



export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.cardSize = React.createRef();
  }

  componentDidMount() {
    const container = document.querySelector('.tb_gp_post_container');
    setTimeout(() => {
      if (this.cardSize.current.offsetWidth < 250) {
        container.classList.add('tb_gp_post_wrapper2x');
      }
      if (this.cardSize.current.offsetWidth < 120) {
        container.classList.add('tb_gp_post_wrapper1x');
      }
      if (suiteTrack) themeImpressionTracking(this.cardSize)
    }, 100);
  }

  render() {
    const { itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA } = this.props;
    const cardSize = {
      width: `${adjustWidth}%`, padding: personalization.padding / 2
    };
    const mediaType = itemData.type !== 1 ? true : false;
    const isYoutubePost = (itemData.network.id === 7) ? true : false

    const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
    const isShopIcon = (isProduct && itemData.hotspot === 0) ? true : false
    const isHotspot = itemData.hotspot && isProduct ? true : false

    const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
    const overlay = isTagbox ? 'tb_gp_color_overlay' : 'tb_gp_post_overlay-' + themeMode
    const overlayStyle = { backgroundColor: isTagbox ? ThemeRule.backgroundColor : '' }

    return (
      <div id={`tb-gp-post-${itemData.id}`} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} className={`tb_gp_post_wrapper ${isTagEmbed ? 'tb_gp_post_wrapper_te' : ''}`} style={cardSize} tb-network={itemData.network.id} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0} tabIndex="0" role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${itemData.content}`} ref={this.cardSize}>

        <div className={`tb_gp_post_in${isTagEmbed ? ' tb_gp_post_ani' : ''}`} onClick={clickToShowPopUp(itemIndex, itemData)}>
          <div className="tb_gp_post_media_wrapp">
            {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_gp_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : null}
            {mediaType ? <Media key={`img_gly_${itemData.id}`} itemData={itemData} wallID={wallID} themeID={personalization.widgetTheme} ownerId={ownerId} /> : ''}

            {isTagEmbed || !isYoutubePost ?
              <div className={`tb_gp_contant_${isTagEmbed ? ' tb_gp_hover_content_' : ''}`}>
                <div className="tb_gp_contant__in">
                  <div className="tb_gp_content__">
                    <AuthorDetails key={`img_gly_${itemData.id}`} adjustWidth={adjustWidth} postTime={itemData.createdAt} network={itemData.network} ownerId={ownerId} author={itemData.author} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
                    {!ThemeRule.hideContent || itemData.type == 1 ? <PostContent item={itemData} contentClass="tb_gp_content" content={itemData.content} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : ''}
                  </div>
                  <div className={`tb_gp_post_overlay ${overlay}`} style={overlayStyle}><div></div></div>
                </div>
              </div>
              : ''}
            <div className="tb_gp_social_">
              {isTagEmbed || !isYoutubePost ? <Network networkClass={'tb_gp_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /> : ''}
            </div>

          </div>
        </div>
      </div>
    );
  }
}